@import url('https://fonts.googleapis.com/css2?family=Mogra&display=swap');

.task-list-container {
    padding: 2rem;
    background-color: rgba(31, 27, 27, 0.9);
    border-radius: 10px;
    max-width: 600px;
    margin: 20px auto;
    box-shadow: 0 4px 8px rgb(255, 254, 254);
    font-family: "Mogra", system-ui;
  font-weight: 400;
  font-style: normal;
  }
  
  .task-list {
    list-style-type:none;
    padding: 0;
  }
  
  .task-item {
    background-color: skyblue;
    color: #fff;
    padding: 1rem;
    margin: 1rem 0;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
  }
  .task-item:hover {
    transform: translateY(-2px);
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.3);
}
  
  .task-content {
    display: flex;
    align-items: center;
  }
  
  .task-item span {
    margin-right: 1rem;
    font-size: 1.2rem;
  }
  
  .task-actions {
    display: flex;
    gap: 10px;
  }
  
  .task-actions button {
    padding: 5px 10px;
    font-size: 1rem;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-family: "Mogra", system-ui;
  font-weight: 400;
  font-style: normal;
  }
  
  .edit-btn {
    background-color: #4caf50;
  }
  
  .delete-btn {
    background-color: #f44336;
  }
  
  .task-actions button:hover {
    opacity: 0.8;
  }
  
  .edit-task input {
    padding: 8px;
    font-size: 1rem;
    margin-right: 10px;
    border-radius: 5px;
    font-family: "Mogra", system-ui;
  font-weight: 400;
  font-style: normal;
  }
  
  .edit-task button {
    background-color: #4caf50;
    color: white;
  }
  
  .edit-task button:hover {
    opacity: 0.8;
  }
  
  .edit-task button:last-child {
    background-color: #f44336;
  }

  .task-actions-edit button{
    padding: 5px 10px;
    font-size: 1rem;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 0.5rem;
    font-family: "Mogra", system-ui;
  font-weight: 400;
  font-style: normal;
  }
  .no-task-content{
    color: #ffffff;
    text-align: center;
    font-size: 1.2rem;
  }