@import url('https://fonts.googleapis.com/css2?family=Mogra&display=swap');

.input-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #875F9A;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    width: 100%;
    max-width: 500px;
    margin: 20px 0;
    opacity: 0.7;
    
}

.input-container h1 {
    font-size: 2rem;
    color: #ffffff;
    margin-bottom: 18px;
    font-family: "Mogra", system-ui;
  font-weight: 400;
  font-style: normal;
}

.input-container input {
    padding: 10px;
    font-size: 1.1rem;
    width: 100%;
    max-width: 400px;
    margin-bottom: 15px;
    border-radius: 5px;
    border: 1px solid #ccc;
}

.input-container button {
    padding: 10px 20px;
    font-size: 1.1rem;
    background-color: #24d42a;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    font-family: "Mogra", system-ui;
  font-weight: 400;
  font-style: normal;
}

.input-container button:hover {
    background-color: #45a049;
}

.input-container input::placeholder{
    font-family: "Mogra", system-ui;
  font-weight: 400;
  font-style: normal;
  color: rgba(11, 11, 11, 0.8);
}